.optionbox- {
  display: inline-block;
  vertical-align: top;
  color: black;
  background-color: white;
  border: solid 1px darkgray;
  overflow: hidden;
}

.optionbox-container {
  max-height: 67vh;
  overflow-y: auto;
}

.optionbox-group {
  display: inline-block;
  vertical-align: top;
}

.optionbox-group-label {
  display: block;
  color: darkgray;
  padding: 0.3em 0.8em;
  transition: all 0.3s;
  background: blue;
}

.optionbox-item {
  display: block;
  position: relative;
  padding: 0.3em 0.5em 0.3em 2em;
  text-align: left;
  max-height: 4em;
  transition: all 0.3s;
}
.optionbox-item::before {
  content: "";
  position: absolute;
  top: 0.6em;
  left: 0.6em;
  width: 0.6em;
  height: 0.6em;
  border-radius: 0.5em;
  background-color: darkgray;
}
.optionbox-item:hover::before {
  background-color: deepskyblue;
  transition: all 0.3s;
}
.optionbox-item:hover::after {
  border-top-color: deepskyblue;
  transition: all 0.3s;
}

.optionbox-radio {
  /* hide visually but keep screen reader accessible */
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}

.optionbox-alt {
  display: block;
  max-height: 2em;
  transition: all 0.3s;
}

.optionbox-.closed .optionbox-item:not(.selected),
.optionbox-.closed .optionbox-alt,
.optionbox-.closed .optionbox-group-label {
  max-height: 0;
  padding-bottom: 0;
  padding-top: 0;
  overflow: hidden;
  opacity: 0;
}
.optionbox-.closed .optionbox-container {
  overflow-y: hidden;
}
.optionbox-.closed .optionbox-group {
  display: inline;
}

.optionbox-item.selected {
  padding-left: 0.5em;
  padding-right: 2em;
}
.optionbox-item.selected::before {
  left: -0.9em;
  opacity: 0;
}
.optionbox-item.selected::after {
  content: "";
  position: absolute;
  top: 0.7em;
  right: 0.5em;
  width: 0;
  height: 0;
  border-left: 0.3em solid transparent;
  border-right: 0.3em solid transparent;
  border-top: 0.5em solid gray;
}